/**
 * å°æ¥å¿æ°ç»åºååä¸º Loki æ ¼å¼
 * @param {Array} logs - æ¥å¿æ°ç»
 * @returns {Uint8Array} - åºåååç Loki æ ¼å¼æ°æ®
 */
export default function logEncoder(logs) {
    // è¿éæ¯æ¡æ¥å¿åç¬ä½ä¸ºä¸æ¡ valueï¼ä¸æ¥å°åä¸ä¸ª streamï¼å¯æ ¹æ®éè¦èªå®ä¹æ ç­¾ï¼
    const streamLabels = {
      host: location.hostname,
    };
    
    const values = logs.map(item => {
      // Loki éè¦çº³ç§çº§æ¶é´æ³å­ç¬¦ä¸²
      const ts = (item.time ? (item.time * 1e6) : (Date.now() * 1e6)).toString();
      // messageåå®¹å»ºè®®ä¸ºå­ç¬¦ä¸²ï¼è¿éç´æ¥åºååæ´ä¸ªitem
      return [ts, JSON.stringify(item)];
    });
    
    const lokiPayload = {
      streams: [
        {
          stream: streamLabels,
          values: values
        }
      ]
    };
    return new TextEncoder().encode(JSON.stringify(lokiPayload));
}